/* TicTac */
.tictac {
  width: 900px;
  height: 900px;
  border-radius: 20px;
  background-color: white;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 20px;
}
.container {
  background-color: #eaf0f1;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.box {
  background-color: #8b79e6;
  width: 280px;
  height: 280px;
  border-radius: 15px;
  font-size: 200px;
  display: flex;
  justify-content: center;
  color: white;
}
.box:hover {
  background-color: #ffc0cb;
  cursor: pointer;
}
.winner {
  font-size: 100px;
  color: #8b79e6;
}
