.quadratic-solver {
  max-width: 700px;
  margin: 20px auto;
  padding: 30px;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  transform: translateY(-10px); 
  animation: slideIn 1s ease-out; 
}

.input-container {
  margin-bottom: 25px;
}

input[type="number"] {
  margin: 8px;
  padding: 12px;
  width: 120px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input[type="number"]:focus {
  border-color: #4caf50;
  box-shadow: 0 0 10px rgba(76, 175, 80, 0.4);
  outline: none;
}


button {
  padding: 12px 24px;
  background: linear-gradient(
    45deg,
    #007bff,
    #4caf50
  ); 
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  border-radius: 8px;
  transition: background 0.3s ease, transform 0.2s ease;
  width: 100%;
  margin-top: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

button:hover {
  background: linear-gradient(
    45deg,
    #0056b3,
    #2e8b57
  ); 
  transform: translateY(-3px);
}

button:active {
  transform: translateY(0);
}

button:focus {
  outline: none;
}


.matrix-input-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-top: 20px;
  transition: all 0.3s ease;
}

.matrix-input-container input {
  width: 45%;
  margin-bottom: 15px;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.matrix-input-container input[type="number"]:focus {
  border-color: #4caf50;
  box-shadow: 0 0 10px rgba(76, 175, 80, 0.4);
}

.matrix-input-container h3 {
  width: 100%;
  font-size: 1.3rem;
  font-weight: 600;
  margin-top: 20px;
  color: #4f4f70;
  text-align: left;
}


@media (max-width: 768px) {
  .quadratic-solver {
    width: 90%;
    padding: 20px;
  }

  .matrix-input-container {
    flex-direction: column;
    align-items: center;
  }

  .matrix-input-container input {
    width: 80%;
    margin: 10px 0;
  }

  button {
    width: 100%;
    padding: 14px 24px;
  }

  h2 {
    font-size: 1.8rem;
  }

  h3 {
    font-size: 1.2rem;
  }
}

