.KCalculatorWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
  box-sizing: border-box;
}

.KCalculator {
  position: relative;
  padding: 20px;
  border-radius: 12px;
  background-color: #f4c2c2;
  box-shadow: 0 4px 10px rgba(61, 61, 61, 0.25);
  min-width: 300px;
  max-width: 350px;
}

.CalcSticker {
  position: absolute;
  top: 0;
  right: 0;

  width: 100px;
  transform: rotate(-15deg);
  border-radius: 12px;
  transition: transform 0.5s ease;
}

.CalcSticker:hover {
  transform: rotate(0deg) scale(1.1);
}

.Kdisplay input {
  font-size: 32px;
  text-align: left;
  padding: 15px 20px;
  width: 100%;
  box-sizing: border-box;
  background-color: #4b4a4a;
  color: white;
  border-radius: 10px;
  border: none;
  outline: none;
  overflow-x: auto;
  white-space: nowrap;
  margin-bottom: 15px;
}

.buttonRow {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.buttonRow input {
  border: none;
  outline: none;
  width: 60px;
  height: 60px;
  font-size: 18px;
  border-radius: 10px;
  background-color: #c0b19c;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.buttonRow input:hover {
  background-color: #ad9f8c;
}

.SignEqual {
  flex: 1;
}

/* For the row with = button */
.buttonRow:last-child input[value="="] {
  flex: 1;
}

/* Adjust the 0 and 00 buttons to be same size as others */
.buttonRow:last-child input[value="0"],
.buttonRow:last-child input[value="00"] {
  flex: 0 0 auto;
  width: 60px;
}
