.formula-book-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 40px 20px;
}

.formula-flipbook {
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  overflow: hidden;
}

.page {
  background-color: rgba(244, 194, 194, 0.95) !important;
  position: relative;
  overflow: hidden;
  padding: 0;
  margin: 0;
  overflow-y: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.page-content {
  padding: 30px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 2;
}

.front-cover,
.back-cover {
  background-color: rgba(244, 194, 194, 0.9) !important;
}

.Kitty-logo {
  width: 180px;
  height: 180px;
  object-fit: contain;
  margin-bottom: 30px;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

h1 {
  color: #333;
  font-size: 28px;
  margin-bottom: 25px;
  padding-bottom: 10px;
  border-bottom: 3px solid #ff9aa2;
  width: 100%;
}

.formula-list {
  margin-top: 20px;
  width: 100%;
}

.formula-list p {
  font-size: 18px;
  margin: 12px 0;
  padding: 8px;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 8px;
  transition: all 0.3s ease;
}

.formula-list p:hover {
  background-color: rgba(255, 255, 255, 0.7);
  transform: translateX(5px);
}

.formula {
  font-family: "Courier New", monospace;
  font-weight: bold;
  font-size: 20px !important;
  color: #333;
  background-color: rgba(255, 255, 255, 0.6) !important;
}

.react-pageflip .page {
  border: 1px solid rgba(255, 154, 162, 0.3);
}

.react-pageflip .page.left {
  border-right: 2px solid #ff9aa2;
}

.react-pageflip .page.right {
  border-left: 2px solid #ff9aa2;
}

@media (max-width: 768px) {
  .formula-flipbook {
    width: 300px !important;
    height: 400px !important;
  }

  h1 {
    font-size: 22px;
  }

  .formula-list p {
    font-size: 16px;
  }

  .Kitty-logo {
    width: 120px;
    height: 120px;
  }
}
